import React, { Component } from 'react';
import Exam from '../components/Exam';
import '../styles/menu.css';

class Menu extends Component {
	constructor() {
		super();
		this.state = {
			exams: []
		}
	}

	componentDidMount(){
		fetch("https://gist.githubusercontent.com/hifebriansyah/5f62028cf655a6b5af592986057d88ef/raw/exams.json")
			.then(response => {
				return response.json();
			})
			.then(result => {
				let exams = result.data.map((exam, i) => {     
					return (
						<Exam 
							name={exam.name}
							id={exam.id}
							items={exam.items}
							content={exam.content}
							link={exam.link}
							key={exam.id}/>
					)                 
				});

				this.setState({exams})
			});
	}

	render() {
		return (
			<div className="menu page">
				{this.state.exams}
			</div>
		);
	}
}

export default Menu;
