import React from "react";
import '../styles/exam.css';
import { Link } from "react-router-dom";

const Exam = (props) => {
	let item = props.items.map((item, i) => {
		return (
			<tr key={i}>
				<td>{item.total}</td>
				<td>{item.content}</td>
				<td className="green">{item.price}</td>
			</tr>
		)                 
	});

    return(
		<div className="exam">
			<div className="top" style={{backgroundImage:'linear-gradient(rgba(100, 100, 100, 0), rgba(100, 100, 100, 0.8)), url(/'+props.id+'.jpeg)'}}>
				<div>
					<h1>{props.name}</h1>
					<p>{props.content}</p>
				</div>
			</div>
			<div className="bottom">
				<span>This section includes</span>  :
				<table>
					<tbody>
						{item}
					</tbody>
				</table>
				<Link to={"/questions/"+props.link} className="btn">Let's go!</Link>
			</div>
		</div>
    )
}

export default Exam;
